  display: flex;
  width: fit-content;
  margin: auto;
  padding: 8px;
  background-color: #ffffff;
  border: 1px solid #CCCCCC;
  border-radius: 24px;
}

.tabsContainer .tab.active{
  background-color: red;
}

.tabsContainer .tab{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 54px;
  width: 200px;
  padding: 8px;
  border-radius: 24px;
}

.previewContainer {
  display: flex;
  align-items: center;
}

.previewContainer div {
  margin: 0 8px;
}

.preview {
  width: 50px;
  height: 50px;
  border: 1px solid #CCCCCC;
  display: inline-block;
  border-radius: 8px;
}

.tooltip {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 4px;
  z-index: 999;
  margin: 0;
  font-size: .9rem;
  padding:8px;
}


/* Tooltip arrow */
.tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.8) transparent transparent transparent;
}


.grid {
  content: "";
  display: block;
  position: relative;
  z-index: -1;
  top: 0;
  left: -8px;
  width: 50px;
  height: 50px;
  border-radius: 8px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
}

.textColor{
  padding: 10px;
  display: inline-block;
  border-radius: 10px;
  background-color: #F0F0F0;
}

.copy_icon:hover{
  cursor: pointer;
}

.textColor:hover{
  background-color: #D4D9E8;
}

input{
